.Marketplace {
	display: flex;
	justify-content: space-evenly;
	
    text-align: center;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    overflow-y: scroll;
    position: fixed;
    background-color: rgb(183, 178, 178);
}

body {
    overflow-x: hidden;
    overflow-y: hidden;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
}

.offerList {
	display: flex;
	justify-content: space-evenly;
	flex-direction: row;
	flex-wrap: wrap;
}

.offerContainer {
	display: flex;
	justify-content: space-evenly;
	width: clamp(300px, 90vw, 350px);
	height: clamp(200px, 90vw, 250px);
	margin: 5px;
}

.offer {
	display: block;
    border-radius: 16px;
	height: 95%;
	width: 95%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    transition: 200ms;
}

.offer:hover {
	box-shadow: rgba(0, 0, 0, 0.24) 0px 8px 12px;
	height: 100%;
	width: 100%;
}

.offer .imgContainer {
	width: 100%;
	height: 40%;
	background-color: rgb(51, 50, 50);
}

.offer img {
	max-width: 100%;
	max-height: 100%;
}

.Header {
    position: sticky;
    top: 0px;
    z-index: 100;
    display: flex;
    background-color: #000000;
    width: 100%;
    height: 50px;
}